<template>
  <div class='goods'>
    <div class="menu-wrapper" ref='menuWrapper'>
      <ul>
        <li v-for="(item,index) in goods" :key='index' class='menu-item' :class="{'current':index === currentIndex}" @click="selectMenu(index)">
          <span class='text border-1px'><span v-show='item.type>0' class='icon' :class='classMap[item.type]'></span>{{ item.name }}</span>
        </li>
      </ul>
    </div>
    <div class="food-wrapper" ref='foodsWrapper'>
      <ul>
        <li v-for='(item,index) in goods' :key='index' class='food-list food-list-hook'>
          <h1 class="title">{{ item.name }}</h1>
          <ul>
            <li  @click="selectFood(food)" v-for='(food,foodIndex) in item.foods' class='food-item border-1px' :key='foodIndex'>
              <div class="icon">
                <img :src="food.icon" alt="" width="57" height="57">
              </div>
              <div class="content">
                <h2 class="name">{{ food.name }}</h2>
                <p class="desc">{{ food.description }}</p>
                <div class="extra">
                  <span class="count">月售{{ food.sellCount }}份</span><span>好评率{{ food.rating }}%</span>
                </div>
                <div class="price">
                  <span class="now">￥{{ food.price }}</span><span class="old" v-show='food.oldPrice'>￥{{ food.oldPrice }}</span>
                </div>
                <div class="cartcontrol-wrapper">
                  <cartcontrol :food="food" @cartAdd='_drop'></cartcontrol>
                </div>
              </div>
            </li>
          </ul>
        </li>
      </ul>
    </div>
    <shopcart ref='shopcart' :select-foods='selectFoods' :delivery-price="seller.deliveryPrice" :min-price="seller.minPrice"></shopcart>
    <food :food='selectedFood' @cartAdd='_drop' ref='food'></food>
  </div>
</template>

<script>
// import axios from 'axios'
import Bscroll from 'better-scroll'
import cartcontrol from '@/components/cartcontrol/cartcontrol'
import shopcart from '@/components/shopcart/shopcart'
import food from '@/components/food/food'
const response = require('@/common/data/goods.json')
const ERR_OK = 0

export default {
  props: {
    seller: {
      type: Object
    }
  },
  data() {
    return {
      goods: [],
      classMap: ['decrease', 'discount', 'special', 'invoice', 'guarantee'],
      listHeight: [],
      scrollY: 0,
      selectedFood: {}
    }
  },
  computed: {
    currentIndex() {
      // 获取索引的位置
      for (let i = 0; i < this.listHeight.length; i++) {
        let height1 = this.listHeight[i]
        let height2 = this.listHeight[i + 1]
        if (!height2 || (this.scrollY >= height1 && this.scrollY < height2)) {
          return i
        }
      }
      return 0
    },
    selectFoods() {
      // 遍历选中的food
      let foods = []
      this.goods.forEach(good => {
        good.foods.forEach(food => {
          if (food.count) {
            foods.push(food)
          }
        })
      })
      return foods
    }
  },
  created() {
    // axios
    //   .get('http://192.168.1.3:8080/api/goods')
    //   .then(res => {
    //     let result = res.data
    //     if (result.errno === ERR_OK) {
    //       this.goods = result.data
    //       this.$nextTick(() => {
    //         // 因为vue是异步渲染的，所以用$nextTick()方法
    //         this._initScroll()
    //         this._calculateHeight()
    //       })
    //     }
    //   })
    //   .catch()
    if (response.errno === ERR_OK) {
      this.goods = response.data
      this.$nextTick(() => {
        this._initScroll()
        this._calculateHeight()
      })
    }
  },
  methods: {
    selectMenu(index) {
      let foodList = this.$refs.foodsWrapper.getElementsByClassName(
        'food-list-hook'
      )
      let el = foodList[index]
      this.foodsScoll.scrollToElement(el, 300)
    },
    selectFood(food) {
      this.selectedFood = food
      this.$refs.food.show()
    },
    _initScroll() {
      // 初始化滚动条
      this.menuScoll = new Bscroll(this.$refs.menuWrapper, {
        click: true
      })
      this.foodsScoll = new Bscroll(this.$refs.foodsWrapper, {
        probeType: 3,
        click: true
      })
      // 实时的拿到滚动的位置
      this.foodsScoll.on('scroll', pos => {
        this.scrollY = Math.abs(Math.round(pos.y))
      })
    },
    _calculateHeight() {
      // 计算foodlist高度
      let foodList = this.$refs.foodsWrapper.getElementsByClassName(
        'food-list-hook'
      )
      let height = 0
      this.listHeight.push(height)
      for (let i = 0; i < foodList.length; i++) {
        let item = foodList[i]
        height += item.clientHeight
        this.listHeight.push(height)
      }
    },
    _drop(target) {
      // 异步调用自组建实现抛物线动画，避免和小球滚动动画堵塞，优化体验
      this.$nextTick(() => {
        this.$refs.shopcart.drop(target)
      })
    }
  },
  components: {
    shopcart,
    cartcontrol,
    food
  }
}
</script>

<style scoped lang='stylus'>
@import '../../common/stylus/mixin.styl'
.goods
  display flex
  position absolute
  top 174px
  bottom 46px
  width 100%
  overflow hidden
  .menu-wrapper
    flex 0 0 80px
    width 80px
    background #f3f5f7
    .menu-item
      display table
      height 54px
      width 56px
      padding 0 12px
      line-height 14px
      &.current
        position relative
        z-index 10
        margin-top -1px
        background white
        font-weight 700
        .text
          border-none()
      .icon
        display inline-block
        width 12px
        height 12px
        margin-right 2px
        background-size 12px 12px
        background-repeat no-repeat
        vertical-align top
        &.decrease
          bg-image('./decrease_3')
        &.discount
          bg-image('./discount_3')
        &.guarantee
          bg-image('./guarantee_3')
        &.invoice
          bg-image('./invoice_3')
        &.special
          bg-image('./special_3')
      .text
        display table-cell
        width 56px
        vertical-align middle
        border-1px(rgba(7, 17, 27, 0.1))
        font-size 12px
  .food-wrapper
    flex 1
    .title
      padding-left 14px
      height 26px
      line-height 26px
      border-left 2px solid #d9dde1
      font-size 12px
      color rgb(147, 153, 159)
      background #f3f5f7
    .food-item
      display flex
      margin 18px
      padding-bottom 18px
      border-1px(rgba(7, 17, 27, 0.1))
      &:last-child
        border-none()
        margin-bottom 0
      .icon
        flex 0 0 57px
        margin-right 10px
      .content
        flex 1
        .name
          margin 2px 0 8px 0px
          height 14px
          font-size 14px
          line-height 14px
          color rgb(7, 17, 27)
        .desc, .extra
          line-height 10px
          font-size 10px
          color rgb(147, 153, 159)
        .desc
          margin-bottom 8px
          line-height 12px
        .extra
          .count
            margin-right 12px
        .price
          font-weight 700
          line-height 24px
          .now
            margin-right 8px
            font-size 14px
            color rgb(240, 20, 20)
          .old
            text-decoration line-through
            font-size 10px
            color rgb(147, 153, 159)
        .cartcontrol-wrapper
          position absolute
          right 0
          bottom 12px
</style>





