<template>
  <div>
    <transition name='move'>
      <div v-show='showFlag' class='food' ref='food'>
        <div class="food-content">
          <div class="image-header">
            <img :src="food.image" alt="">
            <div class="back" @click="hide">
              <i class='icon-arrow_lift'></i>
            </div>
          </div>
          <div class="content">
            <h1 class="title">{{ food.name }}</h1>
            <div class="datail">
              <span class='sell-count'>月售{{ food.sellCount }}份</span>
              <span class="rating">好评率{{ food.rating }}%</span>
            </div>
            <div class="price">
              <span class="now">￥{{ food.price }}</span><span class="old" v-show='food.oldPrice'>￥{{ food.oldPrice }}</span>
            </div>
                      <div class="cartcontrol-wrapper">
            <cartcontrol :food='food' @cartAdd='addCart'></cartcontrol>
          </div>
          <transition name='fade'>
             <div @click.stop.prevent="addFirst" class="buy" v-show="!food.count || food.count === 0">加入购物车</div>
          </transition>   
          </div>
          <split v-show='food.info'></split>
          <div class="info" v-show='food.info'>
            <div class="title">商品信息</div>
            <p class="text">{{ food.info }}</p>
          </div>
          <split></split>
          <div class="rating">
            <h1 class="title">商品评价</h1>
            <ratingselect @contentToggle='contentToggle' @ratingTypeSelect='ratingTypeSelect' ref="ratingselect" :select-type = 'selectType' :only-content='onlyContent' :desc='desc' :ratings = 'food.ratings'></ratingselect>
            <div class="rating-wrapper">
              <ul v-show='food.ratings && food.ratings.length'>
                <li v-show='needShow(rating.rateType,rating.text)' v-for='(rating,ratingIndex) in food.ratings' :key='ratingIndex' class='rating-item border-1px'>
                  <div class="user">
                    <div class="name">{{ rating.username }}</div>
                    <img class="avatar" width='12' height='12' :src='rating.avatar'>
                  </div>
                  <div class="time">{{ rating.rateTime | formatDate}}</div>
                  <p class="text">
                    <i :class="{ 'icon-thumb_up':rating.rateType === 0, 'icon-thumb_down':rating.rateType === 1 }"></i>{{ rating.text }}
                  </p>
                </li>
              </ul>
              <div class="no-rating" v-show='!food.ratings || !food.ratings.length'>暂无评价</div>
            </div>
          </div>
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
import BScroll from 'better-scroll'
import cartcontrol from '@/components/cartcontrol/cartcontrol'
import Vue from 'vue'
import split from '@/components/split/split'
import ratingselect from '@/components/ratingselect/ratingselect'
// 这儿加花括号的意思是引用date里面的formatDate方法  因为date里面没有写default  所以可以要指定引用方法的名称 且能引用里面的多个方法 例如{ formatDate, a }
import { formatDate } from '@/common/js/date.js'

// const POSITIVE = 0
// const NEGATIVE = 1
const ALL = 2

export default {
  props: {
    food: {
      type: Object
    }
  },
  data() {
    return {
      showFlag: false,
      selectType: ALL,
      onlyContent: true,
      desc: {
        all: '全部',
        positive: '推荐',
        negative: '吐槽'
      }
    }
  },
  methods: {
    show() {
      this.showFlag = true
      this.selectType = 0
      this.onlyContent = false
      this.$nextTick(() => {
        if (!this.scroll) {
          this.scroll = new BScroll(this.$refs.food, {
            click: true
          })
        } else {
          this.scroll.refresh()
        }
      })
    },
    hide() {
      this.showFlag = false
    },
    addFirst(event) {
      Vue.set(this.food, 'count', 1)
      this.$emit('cartAdd', event.target)
    },
    addCart(target) {
      this.$emit('cartAdd', target)
    },
    needShow(rateType, text) {
      if (this.onlyContent && !text) {
        return false
      }
      if (this.selectType === ALL) {
        return true
      } else {
        return rateType === this.selectType
      }
    },
    ratingTypeSelect(type) {
      this.selectType = type
      this.$nextTick(() => {
        this.scroll.refresh()
      })
    },
    contentToggle() {
      this.onlyContent = !this.onlyContent
      this.$nextTick(() => {
        this.scroll.refresh()
      })
    }
  },
  filters: {
    formatDate(time) {
      let date = new Date(time)
      return formatDate(date, 'yyyy-MM-dd hh:mm')
    }
  },
  components: {
    cartcontrol,
    split,
    ratingselect
  }
}
</script>

<style scoped lang='stylus'>
@import '../../common/stylus/mixin.styl'
.food
  position fixed
  left 0
  top 0
  bottom 48px
  z-index 30
  width 100%
  background #ffffff
  transform translate3d(0, 0, 0)
  &.move-enter, &.move-leave-to
    transform translate3d(100%, 0, 0)
  &.move-enter-active, &.move-leave-active
    transition all 0.2s linear
  .food-content
    .image-header
      position relative
      width 100%
      height 0
      padding-top 100%
      img
        position absolute
        top 0
        left 0
        width 100%
        height 100%
      .back
        position absolute
        top 10px
        left 0
        .icon-arrow_lift
          display block
          padding 10px
          font-size 20px
          color #eee
    .content
      padding 18px
      position relative
      .title
        line-height 14px
        margin-bottom 8px
        font-size 14px
        font-weight 700
        color rgb(7, 17, 27)
      .datail
        margin-bottom 18px
        line-height 10px
        font-size 0
        height 10px
        .sell-count, .rating
          font-size 10px
          color rgb(147, 153, 159)
        .sell-count
          margin-right 12px
      .price
        font-weight 700
        line-height 24px
        .now
          margin-right 8px
          font-size 14px
          color rgb(240, 20, 20)
        .old
          text-decoration line-through
          font-size 10px
          color rgb(147, 153, 159)
      .cartcontrol-wrapper
        position absolute
        right 18px
        bottom 18px
      .buy
        position absolute
        right 18px
        bottom 18px
        z-index 10
        height 24px
        line-height 24px
        padding 0 12px
        box-sizing border-box
        border-radius 12px
        font-size 10px
        color #ffffff
        background rgb(0, 160, 220)
        opacity 1
        // 如果不加这个动画，由于会瞬间display设置为none 导致小球飞入动画获取getBoundingClientRect()屏幕相对位置失败  最后小球的初始位置会出错
        &.fade-enter, &.fade-leave-to
          opacity 0
        &.fade-enter-active, &.fade-leave-active
          transition all 0.1s
    .info
      padding 18px
      .title
        line-height 14px
        margin-bottom 6px
        font-size 14px
        color rgb(7, 17, 27)
      .text
        padding 0 8px
        line-height 24px
        font-size 12px
        color rgb(77, 85, 93)
    .rating
      padding-top 18px
      .title
        line-height 14px
        margin-left 18px
        font-size 14px
        color rgb(7, 17, 27)
      .rating-wrapper
        padding 0 18px
        .rating-item
          position relative
          padding 16px 0
          border-1px(rgba(7, 17, 27, 0.1))
          .user
            position absolute
            right 0
            top 16px
            font-size 0
            line-height 12px
            .name
              display inline-block
              vertical-align top
              margin-right 6px
              font-size 10px
              color rgb(147, 153, 159)
            .avatar
              border-radius 50%
          .time
            margin-right 6px
            line-height 12px
            font-size 10px
            color rgb(147, 153, 159)
          .text
            line-height 16px
            font-size 12px
            color rgb(7, 17, 27)
            .icon-thumb_up, .icon-thumb_down
              margin-right 4px
              line-height 16px
              font-size 12px
            .icon-thumb_up
              color rgb(0, 160, 220)
            .icon-thumb_down
              color rgb(147, 153, 159)
        .no-rating
          padding 16px 0
          font-size 12px
          color rgb(147, 153, 159)
</style>



