<template>
  <div class="cartcontrol">
    <transition name='move'>
      <div class="cart-decrease icon-remove_circle_outline" v-show="food.count > 0" @click.stop.prevent="decreaseCart"></div>    
    </transition>
    <transition name='fade'>
      <div class="cart-count" v-show="food.count > 0">{{ food.count }}</div>
    </transition>
    <div class="cart-add icon-add_circle" @click.stop.prevent='addCart'></div>
  </div>
</template>

<script>
import Vue from 'vue'
export default {
  props: {
    food: {
      type: Object
    }
  },
  methods: {
    addCart(event) {
      if (!this.food.count) {
        Vue.set(this.food, 'count', 1)
      } else {
        this.food.count++
      }
      this.$emit('cartAdd', event.target)
    },
    decreaseCart() {
      if (this.food.count > 0) {
        this.food.count--
      }
    }
  }
}
</script>

<style scoped lang='stylus'>
.cartcontrol
  font-size 0
  position relative
  .fade-enter, .fade-leave-to
    opacity 0
    transform translate3d(24px, 0, 0)
  .fade-enter-to, .fade-leave
    opacity 1
    transform translate3d(0, 0, 0)
  .fade-enter-active, .fade-leave-active
    transition all 0.3s linear
  .move-enter-active
    animation move 0.4s linear
  .move-leave-active
    animation move 0.4s linear reverse
  @keyframes move
    0%
      opacity 0
      transform rotate(180deg)
      right 0
    100%
      opacity 1
      transform rotate(0)
      right 48px
  .cart-decrease
    display inline-block
    position absolute
    right 48px
    bottom 0px
    font-size 24px
    line-height 24px
    color rgb(0, 160, 220)
  .cart-count
    display inline-block
    vertical-align top
    width 24px
    height 100%
    font-size 10px
    line-height 24px
    text-align center
    color rgb(147, 153, 159)
  .cart-add
    display inline-block
    font-size 24px
    line-height 24px
    color rgb(0, 160, 220)
</style>




