<template>
  <div class="header">
      <div class="content-wrapper">
        <div class="avatar">
          <img :src="seller.avatar" width='64' height="64" alt="">
        </div>
        <div class="content">
          <div class="title">
            <span class="brand"></span>
            <span class="name">{{ seller.name }}</span>
          </div>
          <div class="description">
            {{ seller.description }} / {{ seller.deliveryTime }}分钟送达
          </div>
          <div v-if='seller.supports' class="supports">
            <span class="icon" :class='classMap[seller.supports[0].type]'></span>
            <span class="text">{{ seller.supports[0].description }}</span>
          </div>
        </div>
        <div v-if='seller.supports' class="supports-count" @click='detailShow'>
          <span class="count">{{ seller.supports.length }}个</span>
          <i class='icon-keyboard_arrow_right'></i>
        </div>
      </div>
      <div class="bulletin-wrapper" @click="detailShow">
        <span class="bulletin-title"></span><span class="bulletin-content">{{ seller.bulletin }}</span>
        <i class="icon-keyboard_arrow_right"></i>
      </div>
      <div class="background-image">
        <img :src="seller.avatar" alt="">
      </div>
      <transition name='fade'>
        <div v-show="showDetail" class="detail">
          <div class="detail-wrapper clearfix">
            <div class="detail-main">
              <h1 class="name">{{ seller.name }}</h1>
              <div class="star-wrapper">
                <star :size='48' :score='seller.score'></star>
              </div>
              <div class="title">
                <div class="line"></div>
                <div class="text">优惠信息</div>
                <div class="line"></div>
              </div>
              <ul v-if='seller.supports' class='supports'>
                <li class="support-item" v-for='(item,index) in seller.supports' :key='index'>
                  <span class="icon" :class='classMap[item.type]'></span>
                  <span class="text">{{ item.description }}</span>
                </li>
              </ul>
              <div class="title">
                <div class="line"></div>
                <div class="text">商家公告</div>
                <div class="line"></div>
              </div>
              <div class="bulletin">
                <p class='content'>{{ seller.bulletin }}</p>
              </div>
            </div>
          </div>
          <div class="detail-close">
            <i class="icon-close" @click='detailHedden'></i>
          </div>
        </div>
      </transition>   
  </div>
</template>

<script>
import star from '@/components/star/star'

export default {
  props: ['seller'],
  data() {
    return {
      classMap: ['decrease', 'discount', 'special', 'invoice', 'guarantee'],
      showDetail: false
    }
  },
  methods: {
    detailShow: function() {
      this.showDetail = true
    },
    detailHedden() {
      this.showDetail = false
    }
  },
  components: {
    star
  }
}
</script>

<style scoped lang='stylus'>
@import '../../common/stylus/mixin.styl'
.header
  position relative
  overflow hidden
  color white
  font-weight 200
  background rgba(7, 17, 27, 0.5)
  .content-wrapper
    position relative
    padding 24px 12px 18px 24px
    // 消除两个子组件中间的空白字符造成的间距
    font-size 0
    .avatar
      display inline-block
      vertical-align top
      overflow hidden
      border-radius 2px
    .content
      display inline-block
      margin-left 16px
      font-size 14px
      .title
        margin 2px 0 8px 0
        .brand
          display inline-block
          width 30px
          height 18px
          bg-image('brand')
          background-size 30px 18px
          background-repeat no-repeat
          vertical-align top
        .name
          margin-left 6px
          font-size 16px
          font-weight bold
          line-height 18px
      .description
        margin-bottom 10px
        line-height 12px
        font-size 12px
      .supports
        font-size 0
        .icon
          display inline-block
          width 12px
          height 12px
          margin 0 4px 2px 0
          background-size 12px 12px
          background-repeat no-repeat
          vertical-align top
          &.decrease
            bg-image('./decrease_1')
          &.discount
            bg-image('./discount_1')
          &.guarantee
            bg-image('./guarantee_1')
          &.invoice
            bg-image('./invoice_1')
          &.special
            bg-image('./special_1')
        .text
          line-height 14px
          font-size 10px
    .supports-count
      position absolute
      bottom 16px
      right 12px
      padding 0 8px 0 8px
      height 24px
      border-radius 12px
      background-color rgba(0, 0, 0, 0.2)
      text-align center
      .count
        // vertical-align top
        line-height 24px
        font-size 10px
      .icon-keyboard_arrow_right
        margin-left 2px
        line-height 24px
        font-size 10px
  .bulletin-wrapper
    position relative
    padding 0 22px 0 12px
    height 28px
    line-height 28px
    white-space nowrap
    overflow hidden
    text-overflow ellipsis
    background-color rgba(7, 17, 27, 0.2)
    .bulletin-title
      display inline-block
      vertical-align top
      margin-top 9px
      height 12px
      width 22px
      margin-right 4px
      bg-image('./bulletin')
      background-size 22px 12px
      background-repeat no-repeat
    .bulletin-content
      font-size 10px
      line-height 28px
    .icon-keyboard_arrow_right
      position absolute
      right 12px
      top 10px
      font-size 10px
  .background-image
    position absolute
    left 0
    top -20px
    width 100%
    z-index -1
    img
      width 100%
      filter blur(10px)
  .detail
    position fixed
    z-index 100
    top 0
    left 0
    width 100%
    height 100%
    overflow auto
    opacity 1
    background-color rgba(7,17,27,0.8)
    backdrop-filter blur(10px)
    &.fade-enter-active, &.fade-leave-active
      transition all .5s
    &.fade-enter, &.fade-leave-to /* .fade-leave-active in below version 2.1.8 */ 
      opacity 0
      background-color rgba(7,17,27,0)
    .detail-wrapper
      width 100%
      min-height 100%
      .detail-main
        margin-top 64px
        padding-bottom 64px
        .name
          line-height 16px
          text-align center
          font-size 16px
          font-weight 700
        .star-wrapper
          margin-top 18px
          padding 2px 0
          text-align center
        .title
          display flex
          width 80%
          margin 28px auto 24px auto
          .line
            flex 1
            position relative
            top -6px
            border-bottom 1px solid rgba(255,255,255,0.2)
          .text
            padding 0 12px
            font-weight 700
            font-size 14px
        .supports
          width 80%
          margin 0 auto
          .support-item
            padding 0 12px
            margin-bottom 12px
            font-size 0
            &:last-child
              margin-bottom 0
            .icon
              display inline-block
              width 16px
              height 16px
              vertical-align middle
              margin-right 6px
              background-size 16px 16px
              background-repeat no-repeat
              &.decrease
                bg-image('./decrease_2')
              &.discount
                bg-image('./discount_2')
              &.guarantee
                bg-image('./guarantee_2')
              &.invoice
                bg-image('./invoice_2')
              &.special
                bg-image('./special_2')
            .text
              vertical-align middle
              font-size 12px
              line-height 12px
        .bulletin
          width 80%
          margin 0 auto
          .content
            padding 0 12px
            font-size 12px
            line-height 24px
    .detail-close
      position relative
      width 32px
      height 32px
      margin -64px auto 0 auto
      clear both
      font-size 32px
</style>



